const getBalanceFromAddress = (web3, addressToken, addressUser) =>
  new Promise(async (resolve, reject) => {
    const trc721Contract = new web3.eth.Contract(abiGetBalance, addressToken);
    try {
      const balance = await trc721Contract.methods
        .balanceOfNFT(addressUser)
        .call();
      resolve(balance);
    } catch (err) {
      try {
        const balance = await trc721Contract.methods
          .balanceOf(addressUser)
          .call();
        resolve(balance);
      } catch (err) {
        resolve(null);
        reject("Cannot get balance");
      }
    }
  });

